body {
  font-family: Roboto, Arial, sans-serif;
  overflow: hidden;
}

#flex-layout {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: stretch;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

#flex-layout #content-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: stretch;
  align-items: flex-start;
  flex: 1;
}

#flex-layout #content-wrapper #content {
  width: 100%;
  flex: 1;
}

#flex-layout #content-wrapper > #content {
  overflow: auto;
}

#flex-layout #content-wrapper > #content-with-footer {
  display: flex;
  flex-direction: column;
  width: 100%;
  flex: 1;
  overflow: auto;
}

#flex-layout #content-wrapper > #content-with-footer > #content {
  overflow: visible;
}

#content-wrapper > header,
#content-with-footer > header,
#content > header {
  width: 100%;
  height: 50px;
  background-color: #CCCCCC;
}

#content-wrapper > footer,
#content > footer,
#content-with-footer > footer {
  width: 100%;
  min-height: 40px;
  background-color: #CCCCCC;
  line-height: 20px;
  padding: 10px 15px 10px 15px;
}

#left-sidebar,
#right-sidebar {
  overflow: visible;
  background-color: #DDDDDD;
}

#left-sidebar.hidden,
#right-sidebar.hidden {
  display: none;
}

#left-sidebar {
  width: 230px;
}

#left-sidebar.collapsed {
  width: 50px;
}

#right-sidebar {
  width: 230px;
}

#right-sidebar.collapsed {
  width: 50px;
}

.ss-wrapper {
  overflow: hidden;
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1;
  float: left;
}

.ss-content {
  height: 100%;
  width: calc(100% + 18px);
  padding: 0 0 0 0;
  position: relative;
  overflow: auto;
  box-sizing: border-box;
}

.ss-content.rtl {
  width: calc(100% + 18px);
  right: auto;
}

.ss-scroll {
  position: relative;
  background: rgba(0, 0, 0, 0.1);
  width: 9px;
  border-radius: 4px;
  top: 0;
  z-index: 2;
  cursor: pointer;
  opacity: 0;
  transition: opacity 0.25s linear;
}

.ss-hidden {
  display: none;
}

.ss-container:hover .ss-scroll,
.ss-container:active .ss-scroll {
  opacity: 1;
}

.ss-grabbed {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
